import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Link } from "react-router-dom";
import './SignupApp.css';

import SideBarControl from './components/SideBarControl';
import NavigationBar from './components/NavigationBar';

class SignupComponent extends Component {
	constructor(props){
		super(props);
		this.state = {
			email: '',
			password: '',
			passwordConfirmation: ''
		}
		this.onChange = this.onChange.bind(this);
		this.onSubmit = this.onSubmit.bind(this);
	}
	onChange(e){
		this.setState({
			[e.target.name]: e.target.value
		})
	}
	onSubmit(e){
		e.preventDefault();
		console.log(this.state.username, this.state.password);
	}
	render() {
		return (
			<div className = "row signup-background">
				<div className = "signup-form">
					<form onSubmit = {this.onSubmit}>
						<h1>Create an account</h1>
						<div className = "form-group">
							<div className = "row">
								<div className = "col-md-12">
									<label className = "control-label">Email:</label>
								</div>
							</div>
							<div className = "row">
								<div className = "col-md-4"/>
								<div className = "col-md-4">
									<input name = "email" className = "form-control" value = {this.state.email} onChange = {this.onChange}/>
								</div>
								<div className = "col-md-4"/>
							</div>
							<div className = "row">
								<div className = "col-md-12">
									<label className = "control-label">Password:</label>
								</div>
							</div>
							<div className = "row">
								<div className = "col-md-4"/>
								<div className = "col-md-4">
									<input name = "password" className = "form-control" value = {this.state.password} onChange = {this.onChange}/>
								</div>
								<div className = "col-md-4"/>
							</div>
							<div className = "row">
								<div className = "col-md-12">
									<label className = "control-label">Confirm Password:</label>
								</div>
							</div>
							<div className = "row">
								<div className = "col-md-4"/>
								<div className = "col-md-4">
									<input name = "passwordConfirmation" className = "form-control" value = {this.state.passwordConfirmation} onChange = {this.onChange}/>
								</div>
								<div className = "col-md-4"/>
							</div>
							<div style = {{minHeight: "10px"}}/>
							<div className = "row">
								<div className = "col-md-4"/>
								<div className = "col-md-4">
									<button> Signup </button>
								</div>
								<div className = "col-md-4"/>
							</div>
						</div>
					</form>
				</div>
			</div>
		);
	}
}

export default SignupComponent;
