const styleVar = {
	height: "5vh", 
	backgroundColor: "rgba(96, 229, 34, .4)", 
	position: "fixed", 
	width: "100vw", 
	left: "0vh",
	borderBottomStyle: "solid",
	borderWidth: "5px",
	borderColor: "rgba(256,256,256,0.5)",
	boxShadow: "0 12px 16px 0 rgba(256,256,256,0.24), 0 17px 50px 0 rgba(256,256,256,0.19)"
}

class App extends Component {
	render(){
		  return (
			<Router>
				
				
				<Route path="/" exact component={MainApp} />
				<Route path="/weather_app/" component={WeatherApp} />
				<Route path="/login/" component={LoginComponent}/>
				<Route path="/signup/" component={SignupComponent}/>
				
			</Router>
		  );
	}
}

export default App;
